* {
  font-family: 'Roboto', sans-serif; }

nav {
  position: fixed;
  width: 100%;
  height: 52px;
  background: #212121;
  z-index: 2; }

.menu {
  display: flex;
  justify-content: space-between;
  padding: 14px 70px;
  margin: 0;
  list-style-type: none;

  .menu_link {
    color: #FFFFFF;
    font-weight: 300;
    font-size: 15px; }

  .menu_item {
    border-right: 2px dotted black;
    padding-right: 27px; } }

.subheader {
  position: absolute;
  top: 50px;
  width: 100%;
  height: 87px;
  background: rgba(0, 0, 0, 0.6);

  .subheader_logo {
    display: block;
    min-width: 100%;
    margin-top: 16px; }

  .subheader_official {
    margin-top: 4px;
    opacity: 0,5;
    font-weight: 400;
    font-size: 13px;
    align-items: center;
    color: #FFFFFF; } }

.subheader_call {
  margin-top: 22px;
  font-weight: 300;
  font-size: 12px;
  align-items: center;
  text-align: right;
  color: #FFFFFF; }

.subheader {
  .subheader_phone {
    display: block;
    font-weight: 400;
    font-size: 22px;
    align-items: center;
    text-align: right;
    text-transform: uppercase;
    color: #FFFFFF; }

  .subheader_btn {
    margin-top: 19px;
    position: absolute;
    width: 172px;
    height: 46px;
    text-align: center;
    background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
    border-radius: 6px;
    position: absolute;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    align-items: center;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21); } }

.promo {
  height: 800px;
  background: url('../img/header/car_opening.jpg') center center/cover no-repeat;

  .promo_header {
    color: #FFFFFF;
    font-size: 60px;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 60px;
    margin-top: 151px;
    text-align: center; }

  .promo_subheader {
    color: #FFFFFF;
    font-size: 36px;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 36px;
    margin: 0;
    margin-top: 23px;
    text-align: center; }

  .promo_text {
    font-weight: 400;
    font-size: 18px;
    margin-top: 24px;
    line-height: 24px;
    text-align: center;
    color: #FFFFFF; } }

.promo_btn {
  display: block;
  width: 259px;
  height: 63px;
  margin: 115px auto 0 auto;
  background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
  border: none;
  border-radius: 6px;
  font-weight: 400;
  font-size: 18px;
  line-height: 63px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21); }

.reason {
  height: 849px;
  background: white; }

.label {
  display: block;
  margin: 18px auto 0 auto;
  width: 115px;
  height: 18px;
  background: #000000;
  width: 113px;
  height: 24px;
  font-weight: 400;
  font-size: 11px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  border-radius: 0px; }

.subtitle {
  margin-top: 1px;
  font-weight: 400;
  font-size: 17px;
  line-height: 21px;
  text-align: center;
  color: #222222; }

.reason {
  .title {
    margin-bottom: 0;
    margin-top: 32px;
    font-weight: 700;
    font-size: 38px;
    line-height: 48px;
    text-align: center;
    text-transform: uppercase;
    color: #222222; }

  .reason_block {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    margin-top: 35px;

    .reason_round {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 25px;
      width: 113px;
      height: 113px;
      background: #1EACC7;
      border-radius: 100%; }

    .reasons_descr {
      width: 343px;

      .reasons_subtitle {
        line-height: 21px;
        font-weight: 400;
        font-size: 17px;
        color: #1EACC7; }

      .reasons_text {
        font-weight: 300;
        font-size: 15px;
        line-height: 21px;
        color: #202020; } } } }

.title {
  margin-bottom: 0;
  margin-top: 32px;
  font-weight: 700;
  font-size: 38px;
  line-height: 48px;
  text-align: center;
  text-transform: uppercase;
  color: #222222; }

.mobile {
  min-height: 864px;
  padding: 46px 0 80px 0;
  background: url('../img/mobile/mobile_app.png'); }




